.a{
    height: 100vh;
    display:flex;
    align-items:center;
}

.a-right{
    flex:1
}
.a-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height:100%;

}

.a-card{
    width:60%;
    height:70vh;
    border-radius:30px;
    position: relative;
    overflow: hidden;
}

.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}
.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: darkslategray;
    position:absolute

}
.a-award-img{
    width:120px;
    height:120px;
    border-radius:20px
}

.a-title{
    font-weight: bolder
}
.a-sub{
    margin: 20px 0px;
    font-weight: bold;
}

.a-desc{
    font-weight: 300;
}


.a-award{
    margin-top:50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.a-award-text{
    width: 70%;
}
.a-award-title{
    font-weight: bold;
    color: rgb(175, 24, 206);
    margin-bottom: 10px;
}